import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { EditTypeOfHousingComponent } from './edit-type-of-housing.component';
const routes: Routes = [
  {
    path: '',
    component: EditTypeOfHousingComponent,
    data: {
      title: 'Edit Type of Housing'
    }
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class EditTypeOfHousingRoutingModule {}




