import { Component, OnInit, ViewChild } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { NgForm, NgModel, NgControl, FormsModule } from '@angular/forms';
import { LoopBackAuth, BooksApi, ContainerApi, CityApi, Type_of_housing_masterApi } from '../../../../shared/loopback-sdk';
import { CookieService } from 'angular2-cookie/core';
import { Headers, Http, Response } from '@angular/http';
import { stringify } from 'querystring';
import { ShareService } from '../../../../providers/share-service/share-service';
declare var window: any;
declare var hostname: any;
declare var sdkPrefix;
@Component({
	templateUrl: 'edit-type-of-housing.component.html'
})
export class EditTypeOfHousingComponent {
	public id: number;
	display: boolean = false; result: any;
	submitted: boolean = false;
	typOfHousngName: any;
	busy: any;
	userDetails: any;
	rolename: any;
	loading: boolean = false;
	typOfHusngStatus:boolean = false;
	bookImage:any;
	loginFlagToken:any;
	projectCount : number = 0;
	municipalityCount : number = 0;
	equipmentId : number = 0;
	equipmentPreviousStatus : boolean = false;
	

	editTypeOfHusng={
		"id":0,
		"type_of_housing":"",
		"status": 0,
		//"updated_date":0
	}
	constructor(private loopAuth: LoopBackAuth, private route: ActivatedRoute, private http: Http, private _cookieService: CookieService, private books: BooksApi, private router: Router, private containerApi: ContainerApi, private share: ShareService, private cityApi:CityApi,private typeOfHousingApi:Type_of_housing_masterApi) {
		this.userDetails = this.loopAuth.getCurrentUserData();
		this.rolename = this.userDetails.userDet.roles;
		this.id = this.route.snapshot.params['id'];
		this.loginFlagToken = this._cookieService.get(sdkPrefix+'id');
	}

	ngOnInit(): void {
		this.loading = true;
		var dataArrObj = {
			"id":this.id
		}
		this.typeOfHousingApi.getTypeOfHousingStage(dataArrObj).subscribe(data=>{
			
			if(data){
				let result = data.result[0];
				this.equipmentId = result.id;
				this.typOfHousngName = result.type_of_housing;
				this.typOfHusngStatus = (result.status)?true:false;
				this.projectCount = result.usedProject;
				console.log("projectCount ",this.projectCount);
				this.editTypeOfHusng={
					"id":result.id,
					"type_of_housing": result.type_of_housing,
					"status": result.status,
					//"updated_date":result.updated_date
				}


			}
			this.loading = false;
		},
		err=>{
			console.log("err", err);
			this.loading = false;
		},
		()=>{

		});
	}

	goToListing() {
		// window.location.reload();
		this.router.navigate(["type-of-housing"]);
	}

	async submit(equipmentForm) {
		this.submitted = true;
		if (equipmentForm.valid) {
			this.loading = true;

			let date = new Date();
			let currentdate = date.getTime();

			this.editTypeOfHusng.id = this.equipmentId;
			this.editTypeOfHusng.type_of_housing = this.capital_letter(this.typOfHousngName);
			this.editTypeOfHusng.status = (this.typOfHusngStatus)?1:0;
			//this.editTypeOfHusng.updated_date = currentdate;

			

			//updating equipments-master
		
			// this.ifNameExist(this.editTypeOfHusng).then((total)=>{
			// 	if(total > 0){
			// 		this.loading = false;
			// 		this.result = "Equipment name already exist";
			// 		this.display = true;
			// 	}else{
			// 		console.log("this.editTypeOfHusng ",this.editTypeOfHusng);
					this.typeOfHousingApi.upsertPatch(this.editTypeOfHusng).subscribe((data)=>{
						this.loading = false;
						this.result = "success";
						this.display = true;
					},(err)=>{
						this.loading = false;
						this.result = "Something went wrong";
						this.display = true;
					});
			// 	}
			// },(err)=>{
			// 	this.loading = false;
			// 	this.result = "Something went wrong";
			// 	this.display = true;
			// });

			
		}
	}

	ifNameExist(dataObj){
		return new Promise((resolve,reject)=>{
			try{
				let access_token = this.loopAuth.getAccessTokenId();
				let typeOfHusngString : string = dataObj.type_of_housing
				let cond = { "type_of_housing": typeOfHusngString.toString()};

				let api="/api/type_of_housing_masters/count?where=";
				
				let url = hostname+api+encodeURI(JSON.stringify(cond))+"&access_token="+access_token;
				this.http.get(url).subscribe((data)=>{
					console.log("Count ",data.json());
					resolve(data.json().count);
				},(err)=>{
					reject(err);
				});

			}catch(err){
				reject(err);
			}
		});
		
		
	}

	capital_letter(str) 
	{
		if(str != '' && str != null && str != undefined){
			str = str.split(" ");

			for (var i = 0, x = str.length; i < x; i++) {
				str[i] = str[i][0].toUpperCase() + str[i].substr(1);
			}
	
			return str.join(" ");
		}else{
			return str
		}
		
	}


	
}


