import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from "@angular/forms";
import { HttpModule } from "@angular/http";
import { EditTypeOfHousingComponent } from './edit-type-of-housing.component';
import { EditTypeOfHousingRoutingModule} from './edit-type-of-housing-routing.module';
import { DialogModule } from 'primeng/primeng';
import {BusyModule} from 'angular2-busy';
import {EditorModule} from 'primeng/primeng';

@NgModule({
  imports: [
    CommonModule, 
    FormsModule,
    HttpModule,
    EditTypeOfHousingRoutingModule,
    DialogModule,
    EditorModule,
	  BusyModule
  ],
  declarations: [
    EditTypeOfHousingComponent
  ]
})
export class EditTypeOfHousingModule { }



